import { Component, OnInit } from '@angular/core';
import { ViewproductsService } from '../viewproducts.service';

@Component({
  selector: 'app-viewbooks',
  templateUrl: './viewbooks.component.html',
  styleUrls: ['./viewbooks.component.scss'],
})
export class ViewbooksComponent implements OnInit {
  books = [];
  constructor(private _viewBookService: ViewproductsService) {}

  ngOnInit() {
    this._viewBookService.getbooks().subscribe(
      (res) => (this.books = res.data),
      (err) => console.log(err)
    );
  }
}
