import { Component, OnInit } from '@angular/core';
import { ViewproductsService } from '../viewproducts.service';

@Component({
  selector: 'app-viewjobs',
  templateUrl: './viewjobs.component.html',
  styleUrls: ['./viewjobs.component.scss'],
})
export class ViewjobsComponent implements OnInit {
  jobs = [];
  constructor(private _viewJobService: ViewproductsService) {}

  ngOnInit() {
    this._viewJobService.getjobs().subscribe(
      (res) => (this.jobs = res.data),
      (err) => console.log(err)
    );
  }
}
