import { Component, OnInit } from '@angular/core';
import { ViewproductsService } from '../viewproducts.service';

@Component({
  selector: 'app-viewmobiles',
  templateUrl: './viewmobiles.component.html',
  styleUrls: ['./viewmobiles.component.scss'],
})
export class ViewmobilesComponent implements OnInit {
  mobiles = [];
  constructor(private _viewMobileService: ViewproductsService) {}

  ngOnInit() {
    this._viewMobileService.getmobiles().subscribe(
      (res) => (this.mobiles = res.data),
      (err) => console.log(err)
    );
  }
}
