import { Component, OnInit } from '@angular/core';
import { PostadsService } from '../postads.service';

@Component({
  selector: 'app-postjob',
  templateUrl: './postjob.component.html',
  styleUrls: ['./postjob.component.scss'],
})
export class PostjobComponent implements OnInit {
  jobData = {
    product_type: 'job',
    designation: '',
    company: '',
    salary: '',
    description: '',
    salary_period: '',
    position_type: '',
    experience: '',
    owner_name: '',
    owner_number: '',
    owner_place: '',
  };
  constructor(private _auth: PostadsService) {}

  ngOnInit(): void {}
  postJob() {
    this._auth.postjobService(this.jobData).subscribe(
      (res) => console.log(res),
      (err) => console.log(err)
    );
  }
}
