import { Component, OnInit } from '@angular/core';
import { ViewproductsService } from '../viewproducts.service';

@Component({
  selector: 'app-viewcomputers',
  templateUrl: './viewcomputers.component.html',
  styleUrls: ['./viewcomputers.component.scss'],
})
export class ViewcomputersComponent implements OnInit {
  computers = [];
  constructor(private _viewComputersService: ViewproductsService) {}

  ngOnInit() {
    this._viewComputersService.getcomputers().subscribe(
      (res) => (this.computers = res.data),
      (err) => console.log(err)
    );
  }
}
