import { Component, OnInit } from '@angular/core';
import { ViewproductsService } from '../viewproducts.service';
@Component({
  selector: 'app-viewbikes',
  templateUrl: './viewbikes.component.html',
  styleUrls: ['./viewbikes.component.scss'],
})
export class ViewbikesComponent implements OnInit {
  bikes = [];
  constructor(private _viewBikeService: ViewproductsService) {}

  ngOnInit() {
    this._viewBikeService.getbikes().subscribe(
      (res) => (this.bikes = res.data),
      (err) => console.log(err)
    );
  }
}
