import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';

@Injectable({
  providedIn: 'root',
})
export class PostadsService {
  private _postcarUrl = '/postcar';
  private _postbookUrl = '/postbook';
  private _postbikeUrl = '/postbike';
  private _postjobUrl = '/postjob';
  private _postmobileUrl = '/postmobile';
  private _postcomputerUrl = '/postcomputer';

  constructor(private http: HttpClient) {}
  postcarService(ad) {
    return this.http.post<any>(this._postcarUrl, ad);
  }
  postbikeService(ad) {
    return this.http.post<any>(this._postbikeUrl, ad);
  }
  postbookService(ad) {
    return this.http.post<any>(this._postbookUrl, ad);
  }
  postjobService(ad) {
    return this.http.post<any>(this._postjobUrl, ad);
  }
  postmobileService(ad) {
    return this.http.post<any>(this._postmobileUrl, ad);
  }
  postcomputerService(ad) {
    return this.http.post<any>(this._postcomputerUrl, ad);
  }
}
