import { Component, OnInit } from '@angular/core';
import { PostadsService } from '../postads.service';
import { HttpErrorResponse } from '@angular/common/http';
import { Router } from '@angular/router';

@Component({
  selector: 'app-postbike',
  templateUrl: './postbike.component.html',
  styleUrls: ['./postbike.component.scss'],
})
export class PostbikeComponent implements OnInit {
  bikeData = {
    product_type: 'bike',
    brand: '',
    model: '',
    price: '',
    description: '',
    km_driven: '',
    fuel_type: '',
    year: '',
    no_of_owners: '',
    owner_name: '',
    owner_number: '',
    owner_place: '',
  };
  constructor(private _auth: PostadsService, private _router: Router) {}

  ngOnInit(): void {}
  postBike() {
    this._auth.postbikeService(this.bikeData).subscribe(
      (res) => console.log(res),
      (err) => {
        if (err instanceof HttpErrorResponse) {
          if (err.status === 401) {
            this._router.navigate(['/login']);
          }
        }
      }
    );
  }
}
