import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
@Injectable({
  providedIn: 'root',
})
export class ViewproductsService {
  private _viewcarsUrl = '/viewcars';
  private _viewbikesUrl = '/viewbikes';
  private _viewbooksUrl = '/viewbooks';
  private _viewjobsUrl = '/viewjobs';
  private _viewmobilesUrl = '/viewmobiles';
  private _viewcomputersUrl = '/viewcomputers';

  constructor(private http: HttpClient) {}

  getcars() {
    return this.http.get<any>(this._viewcarsUrl);
  }
  getbikes() {
    return this.http.get<any>(this._viewbikesUrl);
  }
  getbooks() {
    return this.http.get<any>(this._viewbooksUrl);
  }
  getjobs() {
    return this.http.get<any>(this._viewjobsUrl);
  }
  getmobiles() {
    return this.http.get<any>(this._viewmobilesUrl);
  }
  getcomputers() {
    return this.http.get<any>(this._viewcomputersUrl);
  }
}
