import { Component, OnInit } from '@angular/core';
import { PostadsService } from '../postads.service';
@Component({
  selector: 'app-postcar',
  templateUrl: './postcar.component.html',
  styleUrls: ['./postcar.component.scss'],
})
export class PostcarComponent implements OnInit {
  carData = {
    product_type: 'car',
    brand: '',
    model: '',
    price: '',
    description: '',
    km_driven: '',
    fuel_type: '',
    year: '',
    transmission: '',
    no_of_owners: '',
    owner_name: '',
    owner_number: '',
    owner_place: '',
  };
  constructor(private _auth: PostadsService) {}

  ngOnInit(): void {}
  postCar() {
    this._auth.postcarService(this.carData).subscribe(
      (res) => console.log(res),
      (err) => console.log(err)
    );
  }
}
