import { Component, OnInit } from '@angular/core';
import { ViewproductsService } from '../viewproducts.service';

@Component({
  selector: 'app-viewcars',
  templateUrl: './viewcars.component.html',
  styleUrls: ['./viewcars.component.scss'],
})
export class ViewcarsComponent implements OnInit {
  cars = [];
  constructor(private _viewCarService: ViewproductsService) {}

  ngOnInit() {
    this._viewCarService.getcars().subscribe(
      (res) => (this.cars = res.data),
      (err) => console.log(err)
    );
  }
}
