import { Component, OnInit } from '@angular/core';
import { PostadsService } from '../postads.service';

@Component({
  selector: 'app-postbook',
  templateUrl: './postbook.component.html',
  styleUrls: ['./postbook.component.scss'],
})
export class PostbookComponent implements OnInit {
  bookData = {
    product_type: 'book',
    name: '',
    author: '',
    price: '',
    description: '',
    edition: '',
    publisher: '',
    no_of_pages: '',
    owner_name: '',
    owner_number: '',
    owner_place: '',
  };
  constructor(private _auth: PostadsService) {}

  ngOnInit(): void {}
  postBook() {
    this._auth.postbookService(this.bookData).subscribe(
      (res) => console.log(res),
      (err) => console.log(err)
    );
  }
}
