import { Component, OnInit } from '@angular/core';
import { PostadsService } from '../postads.service';

@Component({
  selector: 'app-postcomputer',
  templateUrl: './postcomputer.component.html',
  styleUrls: ['./postcomputer.component.scss'],
})
export class PostcomputerComponent implements OnInit {
  computerData = {
    product_type: 'computer',
    brand: '',
    model: '',
    price: '',
    description: '',
    year: '',
    no_of_owners: '',
    owner_name: '',
    owner_number: '',
    owner_place: '',
  };
  constructor(private _auth: PostadsService) {}

  ngOnInit(): void {}
  postComputer() {
    this._auth.postcomputerService(this.computerData).subscribe(
      (res) => console.log(res),
      (err) => console.log(err)
    );
  }
}
