import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { LandingComponent } from './landing/landing.component';
import { LoginComponent } from './login/login.component';
import { RegisterComponent } from './register/register.component';
import { ViewmobilesComponent } from './viewmobiles/viewmobiles.component';
import { ViewcarsComponent } from './viewcars/viewcars.component';
import { ViewbikesComponent } from './viewbikes/viewbikes.component';
import { ViewjobsComponent } from './viewjobs/viewjobs.component';
import { ViewbooksComponent } from './viewbooks/viewbooks.component';
import { ViewcomputersComponent } from './viewcomputers/viewcomputers.component';
import { PostcarComponent } from './postcar/postcar.component';
import { PostbookComponent } from './postbook/postbook.component';
import { PostbikeComponent } from './postbike/postbike.component';
import { PostjobComponent } from './postjob/postjob.component';
import { PostmobileComponent } from './postmobile/postmobile.component';
import { PostcomputerComponent } from './postcomputer/postcomputer.component';
import { AuthGuard } from './auth.guard';

const routes: Routes = [
  { path: '', component: LandingComponent },
  {
    path: 'login',
    component: LoginComponent,
  },
  {
    path: 'register',
    component: RegisterComponent,
  },
  {
    path: 'viewcars',
    component: ViewcarsComponent,
  },
  {
    path: 'viewbikes',
    component: ViewbikesComponent,
  },
  {
    path: 'viewbooks',
    component: ViewbooksComponent,
  },
  {
    path: 'viewjobs',
    component: ViewjobsComponent,
  },
  {
    path: 'viewcomputers',
    component: ViewcomputersComponent,
  },
  {
    path: 'viewmobiles',
    component: ViewmobilesComponent,
  },
  {
    path: 'postcar',
    component: PostcarComponent,
    canActivate: [AuthGuard],
  },
  {
    path: 'postbike',
    component: PostbikeComponent,
    canActivate: [AuthGuard],
  },
  {
    path: 'postbook',
    component: PostbookComponent,
    canActivate: [AuthGuard],
  },
  {
    path: 'postjob',
    component: PostjobComponent,
    canActivate: [AuthGuard],
  },
  {
    path: 'postmobile',
    component: PostmobileComponent,
    canActivate: [AuthGuard],
  },
  {
    path: 'postcomputer',
    component: PostcomputerComponent,
    canActivate: [AuthGuard],
  },
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule],
})
export class AppRoutingModule {}
