import { Component, OnInit } from '@angular/core';
import { PostadsService } from '../postads.service';

@Component({
  selector: 'app-postmobile',
  templateUrl: './postmobile.component.html',
  styleUrls: ['./postmobile.component.scss'],
})
export class PostmobileComponent implements OnInit {
  mobileData = {
    product_type: 'mobile',
    brand: '',
    model: '',
    price: '',
    description: '',
    year: '',
    no_of_owners: '',
    owner_name: '',
    owner_number: '',
    owner_place: '',
  };
  constructor(private _auth: PostadsService) {}

  ngOnInit(): void {}
  postMobile() {
    this._auth.postmobileService(this.mobileData).subscribe(
      (res) => console.log(res),
      (err) => console.log(err)
    );
  }
}
